@font-face {
  font-family: "Fira Sans";
  src: url("../fonts/FiraSans-Regular.woff2") format("woff2"), url("../fonts/FiraSans-Regular.woff") format("woff");
  font-weight: normal;
  font-display: fallback; }

@font-face {
  font-family: "Fira Sans";
  src: url("../fonts/FiraSans-Medium.woff2") format("woff2"), url("../fonts/FiraSans-Medium.woff") format("woff");
  font-weight: 500;
  font-display: fallback; }

@font-face {
  font-family: "Fira Sans";
  src: url("../fonts/FiraSans-Bold.woff2") format("woff2"), url("../fonts/FiraSans-Bold.woff") format("woff");
  font-weight: bold;
  font-display: fallback; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: bold;
  font-display: fallback; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-ExtraBold.woff2") format("woff2"), url("../fonts/Raleway-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-display: fallback; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  white-space: nowrap;
  width: 1px; }

body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1920px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #dcecff;
  background: url("../img/bg-stars.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 1920px 100%;
  box-shadow: 0 5px 15px 0 rgba(0, 1, 1, 0.2); }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    body {
      background-image: url("../img/bg-stars@2x.png"); } }

.header {
  width: 100%;
  height: 126px;
  display: flex;
  justify-content: center;
  background-color: #ffffff; }
